// As a baseline
h1 {
  margin-top: 0;
}

// Spacing trick for adjusting # anchors down below the fixed header
h1[id]::before, h2[id]::before, h3[id]::before, h4[id]::before, h5[id]::before, h6[id]::before {
  content: "";
  display: block;
  height: 5rem;
  margin-top: -5rem;
  visibility: hidden;
  z-index: 0;
}
