@import 'variables-functions-mixins';

/* Fonts
–––––––––––––––––––––––––––––––––––––––––––––––––– */
$font-stack: 'Montserrat', -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
$monospace-font-stack: 'Source Code Pro', Menlo, monospace;
$monospace-light-font-stack: 'Source Code Pro', Menlo, monospace;


/* Grid (space things out a bit more than normal since this is a brochure site)
–––––––––––––––––––––––––––––––––––––––––––––––––– */
$website-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);


/* Grid (space things out a bit more than normal since this is a brochure site)
–––––––––––––––––––––––––––––––––––––––––––––––––– */
$grid-spacing: 20px;


/* Colors
–––––––––––––––––––––––––––––––––––––––––––––––––– */
$body-font-color: #0f1314;
$header-font-color: #3E4E50;


/* Code
–––––––––––––––––––––––––––––––––––––––––––––––––– */
$code-background: #272822;


/* Stamps
–––––––––––––––––––––––––––––––––––––––––––––––––– */
$stamp-border-radius: 5px;
$stamp-box-shadow: none;
$stamp-font-size: 12px;
$stamp-font-weight: 600;
$stamp-padding: 2px 8px;


/* Forms
–––––––––––––––––––––––––––––––––––––––––––––––––– */
$fieldset-legend-border: none;
$fieldset-legend-color: $body-font-color;
$fieldset-legend-font-size: 16px;
$fieldset-legend-font-weight: 600;
$fieldset-margin: 0 0 40px 0;

$input-addon-background-color: $gray-4;
$input-addon-color: white;
$input-background-color: white;
$input-border: 1px solid $gray-4;
$input-border-radius: 3px;
$input-box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
$input-box-shadow-light: inset 0 1px 1px rgba(0, 0, 0, 0.2);
$input-font-size-desktop: 14px;
$input-font-weight: 500;
$input-padding-y: 11px;
$input-padding-x: 13px;
$input-transition: box-shadow 0.2s ease-in-out;

$radio-bar-background-color: #E5E5E5;

$select-background-color: white;
$select-border: 1px solid $gray-4;
$select-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
$select-color: $body-font-color;
$select-padding: 10px 50px 10px 13px;

$slider-bar-background-color: #E5E5E5;


/* Buttons
–––––––––––––––––––––––––––––––––––––––––––––––––– */
$button-font-weight: 600;
$button-padding: 8px 13px;
$blue-button-background-color: $blue-medium;
$blue-button-hover-background-color: $blue-dark;
$green-button-background-color: $green-medium;
$green-button-hover-background-color: $green-dark;
$orange-button-background-color: $orange-medium;
$orange-button-hover-background-color: $orange-dark;
$purple-button-background-color: $purple-medium;
$purple-button-hover-background-color: $purple-dark;


/* Documentation
–––––––––––––––––––––––––––––––––––––––––––––––––– */
/* These are outside of our color palette but the palette colors don't work very well for these items */
$documentation-read-only-color: dodgerblue;
$documentation-since-color: seagreen;


/* Typography
–––––––––––––––––––––––––––––––––––––––––––––––––– */
$blockquote-border: 2px solid $orange-medium;
$blue-text-color: $blue-medium;
$orange-text-color: $orange-medium;
$yellow-text-color: $yellow-medium;
$h1-size: 44px;
$h1-weight: bold;
$h1-line-height: 52px;
$h2-size: 38px;
$h2-weight: 600;
$h3-size: 26px;
$h3-weight: 400;
$h4-size: 20px;
$h4-weight: 500;
$h5-size: 18px;
$h5-weight: 500;
$h6-size: 16px;
$h6-weight: 500;
