@import 'variables-functions-mixins';


/* Jobs
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.job {
  a, a:hover, a:link, a:visited {
    color: $orange-medium;
  }

  i.fa-lightbulb-on {
    margin-bottom: 75px;
  }

  .apply {
    border-bottom: 1px solid $gray-2;
    border-top: 1px solid $gray-2;
    margin: 50px 0;
    padding: 40px 0;
    text-align: center;
    width: 100%;
  }

  .info {
    i {
      margin-right: 5px;
    }

    span {
      margin-right: 30px;
    }
  }
}
